
.inputs .basket-etf-toggler {
    display: flex;
    position: relative;
    background-color: #f1f5fc;
    color: #a7b1bc;
    font-size: 1.5rem;
    border-radius: 1.8rem;
    padding: 0.8rem 2rem;
    width: 22.3rem;
    justify-content: space-between;
    cursor: pointer;
    box-sizing: content-box;
  }
  
  .inputs .basket-etf-toggler .switch {
    position: absolute;
    width: 50%;
    height: 100%;
    color: #ffff;
    background: #f47a23;
    top: 0;
    left: 0;
    border-radius: 1.8rem;
    padding: 0.8rem 0;
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    transition: left 0.4s ease;
  }
  
  .main-content .etf-baskets {
    display: none;
  }
  
  .main-content .individual-etfs > h2,
  .main-content .etf-baskets > h2 {
    font-size: 2.4rem;
    color: #4a5462;
    margin-bottom: 1.6rem;
  }
  
  .main-content .individual-etfs .recent-launch {
    margin-top: 4.8rem;
  }
  
  .card-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
  }
  .card-container-cta {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .card-container-cta a {
    color: #f6821f;
    font-size: 1.7rem;
    border: 0.15rem solid #f6821f;
    border-radius: 1rem;
    padding: 1.6rem 3.85rem;
    margin-top: 2rem;
    margin-bottom: 4.8rem;
  }
  
  .card-container .card-1 {
    height: 31.5rem;
    border-radius: 1.5rem;
    box-sizing: border-box;
    color: #fff;
    box-shadow: 0px 5.4rem 10rem rgba(10, 4, 60, 0.1);
  }
  
  .card-1 .card-1-inner {
    width: 100%;
    height: 8.1rem;
    border-radius: 1.5rem;
  
    background-size: cover;
    position: relative;
    z-index: 1;
    box-sizing: border-box;
    padding: 1.8rem 2rem;
  }
  
  .nifty-mid-1 .card-1-inner {
    background-image: linear-gradient(
        180deg,
        #213053 0%,
        rgba(33, 48, 83, 0) 100%
      ),
      url('../../../assets/images/card-bg-1.svg');
  }
  
  .sensex-1 .card-1-inner {
    background-image: linear-gradient(
        180deg,
        #213053 0%,
        rgba(33, 48, 83, 0) 100%
      ),
      url('../../../assets/images/card-bg-2.svg');
  }
  
  .nifty-50-1 .card-1-inner {
    background-image: linear-gradient(
        180deg,
        #213053 0%,
        rgba(33, 48, 83, 0) 100%
      ),
      url('../../../assets/images/card-bg-3.svg');
  }
  
  .card-1 .card-1-inner p:first-child {
    font-size: 1.2rem;
    opacity: 0.7;
  }
  
  .card-1 .card-1-inner p:nth-child(2) {
    font-size: 2.4rem;
  }
  
  .card-1 .card-1-inner > img {
    position: absolute;
    right: 2rem;
    bottom: -0.5rem;
  }
  
  .card-1 .card-1-inner > .horse-img {
    width: 3.2rem;
    height: 4.959rem;
  }
  
  .card-1 .card-1-inner > .piggy-img {
    width: 4.687rem;
    height: 4.4rem;
  }
  .card-1 .card-1-inner > .flower-img {
    width: 4.177rem;
    height: 5.3rem;
  }
  
  .card-1 .card-1-main {
    height: 100%;
    margin-top: -8.1rem;
    padding: 9.9rem 2rem 2rem 2rem;
    background-color: #213053;
    box-sizing: border-box;
    border-radius: 1.5rem;
  }
  
  .card-1 .card-1-main > h2 {
    font-size: 1.6rem;
    margin-bottom: 2rem;
  }
  
  .card-1 .card-1-main .card-1-info {
