  }
  
  .card-1-bottom .bottom-buttons img:last-child {
    width: 2rem;
    height: 1.835rem;
  }
  
  .card-1-bottom a {
    padding: 0.85rem 0;
    background-color: #f6821f;
    color: #fff;
    flex: 1;
    border-radius: 1rem;
