#side-content > .returns{
  height: 28.7rem;
  background: #204181 url('../../../assets/images/Line\ 3.svg') no-repeat top !important;
  background-size: contain;
  color: #fff;
}

#side-content > .returns .ret-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
#side-content-mobile > .returns{
  height: 28.7rem;
  background: #204181 url('../../../assets/images/Line\ 3.svg') no-repeat top !important;
  background-size: contain;
  color: #fff;
}

#side-content-mobile > .returns .ret-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .returns .ret-heading h1 {
    font-size: 3.2rem;
  }

.returns .ret-heading .custom-select {
    position: relative;
    font-family: "Lato", sans-serif;
    font-size: 1.8rem;
    width: 17.1rem;
    /* color: rgba(255, 255, 255, 0.8); */
  }

.returns .ret-heading .custom-select select {
    display: none;
}

.select-selected {
    background-color: transparent;
    display: none;
}

.select-selected:after {
    position: absolute;
    content: "";
    top: 1.8rem;
    right: 1rem;
    width: 0;
    height: 0;
    border: 0.6rem solid transparent;
    border-color: #fff transparent transparent transparent;
    border-radius: 0.3rem;
}

.select-selected.select-arrow-active:after {
    border-color: transparent transparent #fff transparent;
    top: 1.2rem;
}

.select-items div,
.select-selected {
    color: rgba(255, 255, 255, 0.8);
    padding: 0.8rem 1.6rem;
    border: 0.1rem solid transparent;
    cursor: pointer;
    user-select: none;
}

.select-items div {
    color: #ffff;
}

.select-items {
    position: absolute;
    background-color: #204181;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 99;
    border: 0.1rem solid #ffff;
}

.select-hide {
    display: none;
}

.select-items div:hover,
.same-as-selected {
    background-color: rgba(0, 0, 0, 0.1);
}

select:focus {
    outline: none;
}

.returns .ret-percentage {
    text-align: center;
    font-size: 6.4rem;
    margin: 3.2rem 0;
}

  .returns a {
    color: #fff;
    background-color: #f6821f;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
    border-radius: 1rem;
  }

  #side-content > .riskometer {
    height: 37.6rem;
    display: flex;
    flex-direction: column;
  }
#side-content-mobile > .riskometer {
    height: 37.6rem;
    display: flex;
    flex-direction: column;
  }
  .riskometer h1,
  .cal-returns h1,
  .documents h1 {
    font-size: 2.4rem;
    color: #4a5462;
  }

  .riskometer .risk-toggle {
    display: flex;
    position: relative;
    font-size: 1.5rem;
    color: #7f828a;
    background-color: #f1f5fc;
    width: 78%;
    box-sizing: border-box;
    padding: 0.7rem 1.8rem;
    border-radius: 1.6rem;
    justify-content: space-between;
    margin: 1.2rem 0 4.1rem 0;
    cursor: pointer;
  }

  .risk-toggle .switch {
    box-sizing: border-box;
    top: 0;
    left: 0;
    position: absolute;
    width: 50%;
    height: 100%;
    color: #fff;
    background-color: #f6821f;
    border-radius: 1.6rem;
    padding: 0.7rem 0;
    text-align: center;
    transition: left 0.5s ease;
  }

  .riskometer img {
    width: 27.24rem;
    height: 22rem;
    align-self: center;
  }

  #side-content > .cal-returns {
    height: 48rem;
    background-image: url('../../../assets/images/Line 4.svg');
    background-position: bottom right;
    background-size: 26rem;
    background-repeat: no-repeat;
  }
#side-content-mobile > .cal-returns {
    height: 48rem;
    background-image: url('../../../assets/images/Line 4.svg');
    background-position: bottom right;
    background-size: 26rem;
    background-repeat: no-repeat;
  }
  .cal-returns p {
    color: #5e6977;
    font-size: 1.8rem;
    margin-top: 3.2rem;
  }

  .cal-returns .amount-input {
    margin-top: 3.3rem;
  }

  .amount-bubble {
    position: relative;
    width: 9.8rem;
    height: 2.75rem;
    background-color: #f6821f;
    color: #fff;
    font-size: 1.8rem;
    padding: 0.7rem;
    text-align: center;
    margin-bottom: 0.6rem;
    border-radius: 0.5rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
  }

  .amount-bubble:after {
    content: " ";
    position: absolute;
    width: 0;
    height: 0;
    left: auto;
    right: 45%;
    bottom: -1.3rem;
    border: 0.7rem solid;
    border-color: #f6821f transparent transparent;
  }

  .cal-returns .input-range .amount-input input[type="range"].styled-slider {
    height: 1rem;
    -webkit-appearance: none;
    width: 100%;
    cursor: pointer;
    margin-top: 1.2rem;
  }

  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider.slider-progress {
    --range: calc(var(--max) - var(--min));
    --ratio: calc((var(--value) - var(--min)) / var(--range));
    --sx: calc(0.5 * 3rem + var(--ratio) * (100% - 3rem));
  }

  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider:focus {
    outline: none;
  }

  /*webkit*/
  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background: #ff7a00;
    border: none;
    box-shadow: none;
    margin-top: calc(1rem * 0.5 - 3rem * 0.5);
    right: 3rem;
  }

  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider::-webkit-slider-runnable-track {
    height: 1rem;
    border: none;
    border-radius: 0.8rem;
    background: #f2f6fa;
    box-shadow: none;
  }
  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider.slider-progress::-webkit-slider-runnable-track {
    background: linear-gradient(to top, darkorange, darkorange) 0 / var(--sx) 100%
        no-repeat,
      #f2f6fa;
  }

  /*mozilla*/
  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider::-moz-range-thumb {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background: #ff7a00;
    border: none;
    box-shadow: none;
  }

  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider::-moz-range-track {
    height: 1rem;
    border: none;
    border-radius: 0.8rem;
    background: #f2f6fa;
    box-shadow: none;
  }

  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider.slider-progress::-moz-range-track {
    background: linear-gradient(to top, darkorange, darkorange) 0 / var(--sx) 100%
        no-repeat,
      #f2f6fa;
  }

  /*ms*/
  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider::-ms-fill-upper {
    background: transparent;
    border-color: transparent;
  }

  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider::-ms-fill-lower {
    background: transparent;
    border-color: transparent;
  }

  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider::-ms-thumb {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background: #ff7a00;
    border: none;
    box-shadow: none;
    margin-top: 0;
    box-sizing: border-box;
  }

  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider::-ms-track {
    height: 1rem;
    border-radius: 0.8rem;
    background: #f2f6fa;
    border: none;
    box-shadow: none;
    box-sizing: border-box;
  }

  .cal-returns
    .input-range
    .amount-input
    input[type="range"].styled-slider.slider-progress::-ms-fill-lower {
    height: 1rem;
    border-radius: 0.8rem 0 0 0.8rem;
    margin: -undefined 0 -undefined -undefined;
    background: linear-gradient(to top, darkorange, darkorange);
    border: none;
    border-right-width: 0;
  }

  .cal-returns .invest-time {
    padding: 2.1rem 2.4rem;
    background-color: #f2f6fa;
    border-radius: 1rem;
    margin-top: 1.2rem;
    margin-bottom: 3.1rem;
  }

  .cal-returns select {
    background-color: #f2f6fa;
    color: #4a5462;
    font-size: 1.5rem;
    width: 100%;
    border: none;
    font-family: "Lato", sans-serif;
    cursor: pointer;
  }

  .cal-returns .cal-bottom {
    display: flex;
    justify-content: space-between;
  }

  .cal-returns .cal-bottom p span {
    display: block;
  }

  .cal-returns .cal-bottom p:first-child span,
  .cal-returns .cal-bottom p:last-child span {
    font-size: 1.4rem;
  }

  .cal-returns .cal-bottom p:last-child {
    color: #ff5824;
    font-size: 2.2rem;
    text-align: right;
  }
  .cal-returns .cal-bottom p:last-child span {
    color: #3ec87b;
  }

  #side-content > .documents {
    height: 33.7rem;
  }
#side-content-mobile > .documents {
    height: 33.7rem;
  }
  .documents .documents-container {
    display: flex;
    flex-direction: column;
    gap: 2.4rem;
    margin-top: 3.2rem;
    color: #4a5462;
  }

  .documents-container .docs-title {
    padding: 1.8rem 2.4rem;
    font-size: 2rem;
    background-color: #f2f6fa;
    border-radius: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .documents-container .docs-title span {
    display: flex;
    align-items: center;
    gap: 1rem;
  }

  .documents-container .docs-title span a img,
  #indicators .equity h1 img,
  #indicators .debt h1 img {
    width: 1.8rem;
    height: 1.8rem;
  }

  .documents-container .docs-title a img {
    width: 1.4rem;
    height: 1.7rem;
  }
