body{
  margin:0;
  padding:0;
  box-sizing:border-box;
  font-family:"Lato",sans-serif;
  font-weight:700;
  scroll-behavior:smooth;
}
header{
  padding:2.6rem 10rem 0;
}
.mobile-line-breaks{
  display:none;
}
.navbar{
  position:fixed;
  width:85.5%;
  padding:0 10rem;
  height:70px;
  display:flex;
  grid-gap:8.9rem;
  gap:8.9rem;
  align-items:center;
  background-color:#fff;
  z-index:11;
  transition:.4s ease-in-out
}
.logo{
  width:10rem;
  height:6rem
}
.navbar #homenavprimary{
  display:flex;
  width:100%
}
.navbar #homenavprimary ul{
  display:flex;
  justify-content:flex-end;
  list-style:none
}
.navbar #homenavprimary ul li a{
  font-size:1.7rem;
  text-decoration:none
}
.navbar .hamburger{
  display:none
}
.navbar #homenavprimary .first-nav-options{
  flex:2 1;
  grid-gap:5.6rem;
  gap:5.6rem;
  padding-left:0
}
.navbar #homenavprimary .first-nav-options li a{
  color:#1d263a;
  transition:.2s ease-in-out
}
.navbar #homenavprimary .first-nav-options li a:hover{
  color:#f6821f
}
.navbar #homenavprimary .sign-up-and-cta{
  grid-gap:4rem;
  gap:4rem
}
.navbar #homenavprimary .sign-up-and-cta li:first-child a{
  color:#1d263a;
  transition:.2s ease-in-out
}
.navbar #homenavprimary .sign-up-and-cta li:first-child a:hover{
  color:#f6821f
}
.navbar #homenavprimary .sign-up-and-cta li:last-child a{
  color:#fff;
  background-color:#f6821f;
  padding:2rem 4.15rem;
  border-radius:1rem
}
.navbar #homenavprimary .sign-up-and-cta li:last-child a:hover{
  background-color:#ff7a00;
  -webkit-transform:scaleY(20px);
  transform:scaleY(20px);
  box-shadow:0 0 2px 0 #333
}
.header-info{
  margin-top:10rem;
  display:flex;
  height:51rem;
  position:relative
}
.header-info .info{
  flex-basis:50%
}
.header-info .info h1{
  font-size:4.7rem;
  color:#213053;
  margin: 3rem 0rem;
}
.header-info .info .info-links{
  display:flex;
  margin-top:.8rem
}
.info .info-links a{
  text-decoration:none;
  font-size:1.7rem;
  border-radius:1rem
}
.info .info-links a:first-child{
  color:#fff;
  background-color:#f6821f;
  padding:2rem 4.15rem;
  margin-right:2.4rem
}
.info .info-links a:last-child{
  color:#1d263a;
  background-color:transparent;
  border:.2rem solid #1d263a;
  padding:1.7rem 2.15rem
}
.video{
  margin-top:6rem;
  display:flex;
  grid-gap:3rem;
  gap:3rem
}
.video div:first-child{
  position:relative
}
.video div:first-child img:first-child{
  width:17.1rem;
  border-radius: 1rem;
}
.video div:first-child img:last-child{
  width:3rem;
  position:absolute;
  top:3.4rem;
  left:6.3rem
}
.video div:last-child{
  align-self:center;
  font-size:1.4rem
}
.video div:last-child h3{
  font-size:2.4rem
}
.video div:last-child h3,.video div:last-child p{
  margin:0
}
.video div:last-child p{
  font-size:1.5rem;
  color:#5e6977;
  opacity:.8
}
.header-info .images{
  position:relative
}
.header-info .images img:first-child{
  position:absolute;
  left:-15rem;
  top:6rem;
  width:76rem
}
.header-info .images img:nth-child(2){
  position:absolute;
  top:5rem;
  left:-3rem;
  width:52rem
}
.header-info .images img:nth-child(3){
  position:absolute;
  width:61.3rem;
  right:-16rem;
  top:29rem;
  -webkit-filter:blur(6.8rem);
  filter:blur(6.8rem);
  z-index:1
}
.header-info .images img:last-child{
  position:absolute;
  bottom:-9rem;
  left:-19rem;
  width:85rem
}
.is-sticky ul{
  background-color:#204181!important;
  border-radius:0 0 16px 16px!important
}
#navbar-2{
  position:absolute;
  bottom:-1.8rem;
  width:100%;
  border-radius:1.6rem 1.6rem 0 0;
  /* transition: .2s ease-in-out; */
}
#navbar-2,#navbar-2 ul{
  z-index:10;
  height:5.5rem
}
#navbar-2 ul{
  background:hsla(0,0%,100%,.1);
  border:.1rem solid rgba(230,236,250,.24);
  -webkit-backdrop-filter:blur(6.4rem);
  backdrop-filter:blur(6.4rem);
  border-radius:1.6rem 1.6rem 0 0;
  display:flex;
  list-style:none;
  margin-top:0;
  align-items:center;
  justify-content:center;
  grid-gap:6rem;
  gap:6rem;
  padding:.5rem 0;
  position:static;
  transition:.6s ease-in-out;
}
#navbar-2 ul li a{
  color:#fff;
  text-decoration:none;
  padding:1.8rem 0;
  font-size:1.7rem;
  opacity:.8
}
.active-option,.first-active-option{
  border-bottom:.5rem solid #ff7b00;
  color:#ff7b00!important
}
#why-to-invest{
  margin-top:3rem;
  padding:0 5rem;
  position:relative;
  z-index:1
}
.why-to-invest{
  overflow:hidden;
  position:relative;
  z-index:4;
  border-radius:1rem;
  background-color:#fef9ed;
  background-image:linear-gradient(hsla(0,0%,100%,.14901960784313725),#fef9ed),url('./assets/images/orange-pattern-y-to-invest.svg');
  background-position:50%;
  color:#4a5462;
  box-shadow:0 55px 114px 0 rgba(0,0,0,.1)
}
#why-to-invest .bg-pattern{
  top:-6.4rem;
  position:absolute;
  left:-6.2rem;
  width:146.1rem;
  z-index:2
}
#why-to-invest .bg-gradient{
  top:.1rem;
  position:absolute;
  left:-6.2rem;
  width:146.1rem;
  z-index:3
}
#why-to-invest .why-to-invest-info{
  overflow:hidden;
  position:relative;
  z-index:3;
  padding:6.1rem;
  border-radius:1rem
}
#why-to-invest .why-to-invest-info h1{
  font-size:4.2rem;
  margin:0
}
#why-to-invest .why-to-invest-info .pointer-container{
  margin-top:3rem;
  display:grid;
  grid-template: 1fr / auto auto auto;
  grid-gap : 6rem;
  padding:0;
  /* grid-gap:10rem;
  gap:10rem;
  flex-wrap: wrap; */
}
.pointer-container .pointer p{
  margin:0 0 3rem
}
.pointer-container .pointer .pointer-count{
  position:relative;
  text-align:center;
  color:#fef9ed;
  margin-top:4rem;
  background:#f6821f;
  width:6.6rem;
  border-radius:1.5rem;
  font-size:3.2rem;
  height:6.6rem;
  display:flex;justify-content:center;
  align-items:center
}
.pointer-container .pointer .pointer-head{
  font-size:2.4rem
}
.pointer-container .pointer .pointer-subtext{
  font-size:1.5rem;
  color:#5e6977;
  margin-bottom:4rem;
  line-height:1.8;
  opacity:.8
}
#starter-etfs{
  padding:0 5rem
}
.starter-etfs{
  padding:10.4rem 6rem 0;
  background:#204181;
  color:#fff;
  position:relative;
  bottom:1.5rem;
  border-radius:1rem;
  height:38rem;
  box-shadow:0 55px 114px 0 rgba(0,0,0,.1);
  z-index:0
}
.starter-etfs .nifty-sensex-container .card-1 .investor-heading{
  font-size:4.8rem;
  margin:2.5rem 0rem;
}
.starter-etfs .nifty-sensex-container .card-1 div:first-child .new-investor{
  color:#ff7a00;
  font-size:1.3rem;
  letter-spacing:.3rem;
  margin:2.5rem 0
}
.investor-heading{
  margin-top:0
}
.starter-etfs .nifty-sensex-container{
  display:flex;
  justify-content:space-between
}
.card-3{
  position:relative;
  transition:.2s ease-in-out
}
.card-3:hover{
  -webkit-filter:drop-shadow(2px 4px 6px #333);
  filter:drop-shadow(2px 4px 6px #333);
  -webkit-transform:scale(1.05);
  transform:scale(1.05)
}
.card-3 a{
  text-decoration:none;
  color:#fff
}
.card{
  width:36.4rem;
  height:38.2rem;
  position:relative;
  background-image:linear-gradient(180deg,#213053,rgba(33,48,83,0) 85%);
  background-position:50%;
  border-radius:1.4rem;
  box-shadow:0 54px 100px rgba(10,4,60,.1);
  color:#fff
}
.card-info{
  position:absolute;
  top:2rem
}
.card .card-info .nifty-sensex-text .light-heading,
.nifty-sensex-container .card-1 .card-3 .nifty-sensex-text .light-heading{
  opacity:.07;
  font-size:8rem;
  position:absolute;
  left:-2rem;
  margin:0;
  /* top:10rem; */
  z-index:1
}
.card-info .main-heading{
  font-size:6rem;
  margin:0 0 0 2.5rem
}
.card-info p{
  font-size:1.5rem;
  margin:0 0 1.4rem 2.5rem;
  opacity:.7
}
.card .card-bg{
  width:36.4rem;
  height:28.4rem;
  position:relative;
  z-index:-1;
  overflow:hidden;
  border-radius:1.5rem
}
.card .card-img{
  background-image:url('./assets/images/nifty.svg');
  background-position:50%;
  width:36.4rem;
  height:39.4rem;
  background-size:59rem;
  position:absolute;
  top:0
}
.card .card-details{
  background:#213053;
  border-bottom-left-radius:1.5rem;
  display:flex;
  padding:3.3rem 2.4rem 2.7rem;
  align-items:center;
  box-sizing:border-box;
  justify-content:space-between;
  border-bottom-right-radius:1.5rem;
  position:absolute;
  bottom:0;
  width:100%;
  z-index:-2;
  font-size:2.1rem
}
.card .card-details p{
  margin:0;
  margin-top: 2rem;
}
.card .card-details p:last-child{
  align-self:end;
  font-size:1.7rem
}
.card .card-details p:last-child span{
  opacity:1;
  margin-right:1rem
}
.vector-img{
  position:absolute;
  bottom:9rem;
  right:3rem;
  width:8.6rem
}
.piggy{
  width:10.5rem!important
}
.horse{
  width:7rem
}
.card .card-inner .vector-img{
  z-index:3;
  position:absolute;
  width:8.2rem;
  bottom:.6rem;right:2rem
}
.nifty-sensex-container .card-1:last-child .card .card-img{
  background-image:url('./assets/images/sensex.svg')
}
#find-the-etf{
  padding:0 5rem
}
.find-the-etf{
  position:relative;
  top:25rem;
  padding:0 6.3rem
}
.find-the-etf>h1{
font-size:4.8rem;
margin: 5rem 0rem;
color:#4a5462
}
.find-the-etf-navbar > ul{
  display:flex;
  padding:0;
  list-style:none;
  grid-gap:2.3rem;
  gap:2.3rem;
  position:relative;
  width:114rem
}
.find-the-etf-navbar > ul > li{
  border-radius:4.6rem;
  padding:1.1rem 3.4rem;
  background:rgba(237,242,251,.8);
  font-size:1.5rem;
  color:#7f828a;
  cursor:pointer;
  transition:.2s ease-in
}
.find-the-etf-navbar > ul > .search-option{
  display:none
}
.find-the-etf-navbar > ul > li:last-child{
  padding:.9rem 4.5rem;
  position:absolute;
  right:0;
  background-color:#ededed
}
.find-the-etf-navbar > ul > li:last-child img{
  position:relative;
  right:3.1rem;
  top:.3rem;
  width:1.8rem;
  height:1.8rem;
  opacity:.5
}
.find-the-etf-navbar > ul > li:last-child{
  z-index:2;
}
.find-the-etf-navbar > ul:last-child #etf-searchbox-container{
  transition:.6s ease-in-out!important;
  width:auto;
  border:1px solid #fff
}
.find-the-etf-navbar > ul > li #close-search-btn{
  position:absolute;
  top:1.1rem;
  z-index:3;
  display:none
}
.find-the-etf-navbar > ul > li:last-child input{
  position:relative;
  bottom:.2rem;
  right:1rem;
  border:none;
  background-color:transparent;
  font-size:1.5rem;
  width:18rem;
  font-family:"Lato",sans-serif;
  font-weight:700
}
.find-the-etf-navbar > ul > li:last-child input:focus{
  outline:none
}
#etf-searchbox-desktop-input{
  color:#707070;
  width:70%;
}
