#find-the-etf .container{
  margin-top:6rem;
  display:grid;
  width:114rem;
  grid-template-columns:1fr 1fr 1fr;
  grid-gap:2rem;
  gap:2rem;
  overflow:hidden;
  box-shadow:0 54px 100px rgba(10,4,60,.1)
}
#find-the-etf .container .card a{
  text-decoration:none;
  color:#fff
}
#find-the-etf .container .card{
  transition:.6s ease-in-out
}
#find-the-etf .container .card:hover{
  -webkit-filter:drop-shadow(0 0 2px #333);
  filter:drop-shadow(0 0 2px #333)
}
.etf-searchbox-desktop-result-c{
  background: transparent;
  position: absolute;
  top:4rem;
  width: 92.5%;
  border-bottom-left-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
  box-shadow: 0 3px 4px 0 #555;
}
.etf-searchbox-desktop-result-c li:first-child a p{
  color: #204181 !important;
}
.etf-searchbox-desktop-result-c li:last-child{
  border-bottom-left-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
  border-bottom: none;
  
  /* background-color: #204181; */
}
.etf-searchbox-desktop-result-c li a p:hover{
  color: #204181;
}
.etf-searchbox-desktop-result-c .etf-searchbox-desktop-result-item{
  border-bottom: 1px solid #afafaf87;
  background: #ededed;
  /* box-shadow: 0px 3px 5px 0 #555; */
  /* border-radius: none !important; */
}

.etf-searchbox-desktop-result-c .etf-searchbox-desktop-result-item a p{
  padding: 1rem;
  text-transform: capitalize;
  color: #707070;
}
#find-etf-mobile-search-container{
  display:none;
  position:fixed;
  width:100%;
  top:0;
  left:0;
  height:1500vh;
  padding-top:10px;
  background-color:#fff;
  flex-direction:column;
  justify-content:flex-start;
  align-items:center;
  z-index:12
}
.find-etf-mobile-searchbox-results{
  width:100%;
  height:90vh;
  overflow:scroll;
}
.find-etf-mobile-searchbox-results:first-child p > a > span{
  color: #204181 !important;
}
.find-etf-mobile-searchbox-results p{
  display:flex;
  align-items:center;
  /* margin-bottom:2.5rem */
  padding: 1rem 3rem;
}
.find-etf-mobile-searchbox-results p img{
  padding-right:10px;
  opacity:.8
}
.find-etf-mobile-searchbox-results p a{
  text-decoration:none!important;
  font-size:2.5rem;
  font-family:"Lato",sans-serif;font-weight:400;
  color:#848484
}
.find-etf-mobile-searchbox-results p a:hover{
  color:#204181
}
.find-etf-mobile-searchbox{
  display:flex;
  width:90%;
  height:50px;
  justify-content:center;
  align-items:center;
  border-radius:20rem;
  text-transform:capitalize!important;
  background-color: #ededed;
  padding: 0rem 1.1rem;
}
.find-etf-mobile-searchbox #etf-mobile-search-clear{
  width:2rem;
  /* padding-left:5px; */
  cursor:pointer
}
.find-etf-mobile-searchbox #etf-mobile-search-back{
  width:2rem;
  padding-right:5px;
  cursor:pointer
}
.find-etf-mobile-searchbox input{
  padding:10px;
  width:80%;
  outline:none;
  border:none;
  background-color:transparent;
  font-size:2rem;
  font-family:"Lato",sans-serif;
  font-weight:400;
  color:#848484
}
.container .card:first-child .card-img{
  background-image:url('./assets/images/find-the-right-etf-card-1.svg')
}
.container .card:nth-child(2) .card-img{
  background-image:url('./assets/images/find-the-right-etf-card-2.svg')
}
.container .card:nth-child(3) .card-img{
  background-image:url('./assets/images/find-the-right-etf-card-3.svg')
}
.container .card:nth-child(4) .card-img{
  background-image:url('./assets/images/find-the-right-etf-card-4.svg')
}
.container .card:nth-child(5) .card-img{
  background-image:url('./assets/images/sensex.svg')
}
.container .card:first-child .card-info .nifty-sensex-text .light-heading,
.container .card:nth-child(4) .card-info .nifty-sensex-text .light-heading{
  top:0
}
#find-the-etf .find-the-etf-cta{
  text-align:center;
  margin-top:4.5rem;
  height: 10rem;
}
#find-the-etf .find-the-etf-cta .arrows{
  display:none
}
#find-the-etf .find-the-etf-cta a{
  border:.2rem solid #f6821f;
  color:#f6821f;
  padding:1.6rem 2.6rem;
  border-radius:1rem;
  font-size:1.7rem;
  text-decoration:none;
  letter-spacing:.04rem
}
#stay-tuned{
  padding:0 11.5rem
}
.stay-tuned{
  position:relative;
  z-index:1;
  top:32rem;
  display:grid;
  grid-template-columns:1fr 1.2fr;
  color:#fff;
  background-color:#204181;
  align-items:center;
  justify-content:center;
  padding:7.5rem 8rem;
  border-radius:1.5rem;
  background-image:url('./assets/images/Line 2.svg'),url('./assets/images/Line 3.svg');
  background-size:auto,contain;
  background-repeat:no-repeat;
  background-position:0 0,45.5rem;
  box-shadow:0 55px 114px 0 rgba(0,0,0,.1)
}
.stay-tuned h1{
  font-size:4.6rem;
  margin:0
}
.stay-tuned p{
  font-size:1.6rem;
  font-weight:700;
  margin:1rem 0 0;
  opacity:.8;
  line-height:1.8
}
.subscribe-email{
  padding:10px
}
.subscribe-email,.subscribe-email form{
  width:100%;
  display:flex;
  justify-content:flex-end
}
.subscribe-email input[type=email]{
  padding:1.7rem 2rem;
  border-radius:1rem;
  width:60%;
  border:none;
  outline:none;
  font-size:1.5rem;
  font-family:"Lato",sans-serif;
  font-weight:700;
  color:#4a5462
}
.subscribe-email input[type=email]:focus{
  color:#4a5462
}
.stay-tuned .subscribe-email .subscribe-email-CTA{
  color:#fff
}
.stay-tuned .subscribe-email .subscribe-email-CTA,
.stay-tuned .subscribe-email .subscribe-email-CTA-disabled{
  font-family:"Lato",sans-serif;
  background-color:#f6821f;
  padding:1.7rem 2rem;
  border-radius:1rem;
  font-size:1.5rem;
  font-weight:400;
  text-align:center;
  text-decoration:none;
  margin-left:-5rem;
  line-height:20px;
  border:none;
  outline:none
}
.stay-tuned .subscribe-email .subscribe-email-CTA-disabled{
  color:#cbcbcb;
  cursor:not-allowed
}
#how-to-choose{
  position:relative;
  top:23rem;
