.etf-details .inner-etf {
  background-image: linear-gradient(0deg, #000000 0%, rgba(33, 48, 83, 0) 100%),
    url("../../../assets/images/etf-BG.svg");
  background-size: contain;
  border-radius: 1.6rem;
  filter: drop-shadow(0rem 5.4rem 10rem rgba(10, 4, 60, 0.1));
  padding: 2.4rem 3.2rem;
  box-sizing: border-box;
  color: #ffff;
  background-repeat: no-repeat;
}

.inner-etf .etf-heading {
  display: flex;
  justify-content: space-between;
}

.inner-etf .etf-heading h1 {
  font-size: 2.8rem;
}

.inner-etf .etf-heading span {
  display: flex;
  gap: 2rem;
}

.etf-heading span img {
  width: 1.8rem;
  height: 2.15rem;
}

.inner-etf .etf-1 {
  display: flex;
  gap: 0.8rem;
  margin-top: 1.6rem;
  margin-bottom: 3.4rem;
}

.inner-etf .etf-1 li {
  color: #000;
  font-size: 1.4rem;
  padding: 0.5rem 1.2rem;
  padding-top: 0.4rem;
  background-color: #ffff;
  border-radius: 1.4rem;
  display: flex;
  align-items: center;
}

.inner-etf .etf-main {
  display: flex;
  justify-content: space-between;
}

.etf-main h1 {
  font-size: 2.4rem;
}

.etf-main h2 {
  display: flex;
  align-items: center;
  font-size: 1.6rem;
  gap: 1rem;
  margin-bottom: 0.6rem;
}

.etf-main h2 img {
  width: 2rem;
  height: 2rem;
}

.etf-main .stars {
  display: flex;
  gap: 1.1rem;
}

.etf-main .stars img {
  width: 2.28rem;
  height: 2.18rem;
}

.inner-etf .etf-info .etf-info-head {
  font-size: 1.6rem;
  margin-top: 2.6rem;
  margin-bottom: 0.8rem;
}

.etf-info .etf-info-body {
  font-size: 1.4rem;
}

.etf-info-body .readMore,
.etf-info-body .viewLess {
  color: #f6821f;
  cursor: pointer;
}

.etf-info-body .read-more-content {
  display: none;
}

.etf-details .bottom-etf {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 2.4rem 3.2rem;
  padding-top: 10.3rem;
  background-color: #204181;
  margin-top: -7.9rem;
  border-bottom-left-radius: 1.6rem;
  border-bottom-right-radius: 1.6rem;
  color: #fff;
  gap: 3.2rem 14rem;
}

.bottom-etf .bot-etf-1 p:first-child {
  font-size: 1.4rem;
  color: #f6821f;
}

.bottom-etf .bot-etf-1 p:last-child {
  font-size: 2.4rem;
}

.bot-etf-1 p:last-child span {
  font-size: 1.4rem;
}

.main-container .main-navbar {
  font-size: 1.7rem;
  color: #5e6977;
  background: rgba(32, 65, 129, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.24);
  border-radius: 1.6rem 1.6rem 0rem 0rem;
  display: flex;
  /* gap: 4.8rem; */
  padding: 0 2rem;
}

.main-navbar li {
  cursor: pointer;
  padding: 2.8rem 2rem;
  opacity: 0.64;
}

.main-navbar .active-option {
  color: #ff7a00;
  border-bottom: 0.4rem solid #ff7a00;
  opacity: 1;
}

#overview-graph {
  padding: 3.4rem 2.3rem 4.8rem 2.3rem;
}

#overview-graph .graph-head {
  display: flex;
  justify-content: space-between;
}

.graph-head h1 {
  font-size: 2.4rem;
  color: #4a5462;
}

.graph-head span {
  display: flex;
  gap: 1.3rem;
}

.graph-head span img {
  cursor: pointer;
}

.graph-head span img:first-child {
  width: 1.6rem;
  height: 1.6rem;
}

.graph-head span .graph-toggle {
  width: 1.5rem;
  height: 1.5rem;
}

.graph-vs {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 2.6rem;
}

.graph-vs > p {
  font-size: 2rem;
}

.graph-vs > div {
  font-size: 1.6rem;
  color: #fff;
  display: flex;
  padding: 1rem 1.8rem;
  box-sizing: border-box;
  border-radius: 0.4rem;
  gap: 6.3rem;
}

.graph-vs .nip-etf {
  background-color: #f6821f;
}

.graph-vs .benchmark {
  background-color: #204181;
  gap: 7.6rem;
}

.graph-vs > div p {
  padding: 0.45rem 0;
}

.graph-vs > div p:last-child {
  border-left: 0.1rem solid rgba(255, 255, 255, 0.1);
  padding: 0;
  padding-left: 2rem;
  font-size: 2rem;
}

.graph-vs .benchmark p:first-child {
  display: flex;
  align-items: center;
  gap: 1.4rem;
}
.graph-vs .benchmark p:first-child img {
  width: 1.35rem;
  height: 1.35rem;
}

.graph {
  position: relative;
}

.graph .graph-bar {
  position: absolute;
  top: 7rem;
  right: 8.4rem;
  width: 17rem;
  height: 4.4rem;
}
.graph .graph-dot {
  position: absolute;
  bottom: 20.1rem;
  right: 19.4rem;
  width: 0.6rem;
  height: 0.6rem;
}

.graph .graph-img {
  width: 69.9rem;
  height: 39.3rem;
}

.graph-filter-bar {
  display: flex;
  color: #4a5462;
  font-size: 1.6rem;
  background-color: #f9f1dc;
  border-radius: 0.8rem;
  padding: 1.2rem 3.8rem;
  gap: 7.6rem;
  margin-bottom: 12.8rem;
  position: relative;
}

.graph-filter-bar li {
  cursor: pointer;
}

.graph-filter-bar .active-filter {
  position: absolute;
  left: 0;
  padding: 1.2rem 3.9rem;
  top: 0;
  background-color: #f6821f;
  color: #fff;
  cursor: pointer;
  transition: left 0.4s ease;
}

.graph-filter-bar .active-filter:after {
  position: absolute;
  content: "";
  top: -2rem;
  right: 3.8rem;
  width: 0;
  height: 0;
  border: 1rem solid transparent;
  border-color: transparent transparent #f6821f transparent;
}

.table-view {
  display: none;
  margin-top: 1.7rem;
  margin-bottom: 5.7rem;
}

.table-view > div {
  display: flex;
  font-size: 1.6rem;
}

.table-view > div p {
  width: 17.5%;
  padding: 2.4rem 0;
}

.table-view .table-view-head p {
  padding: 1.5rem 0;
  color: #a7b1bc;
}

.table-view > div p:first-child {
  width: 30%;
  font-size: 1.4rem;
  font-weight: normal;
}

.table-view > div p:first-child span {
  display: block;
  font-size: 1.6rem;
  color: #f47a23;
  font-weight: bold;
}

.table-view .row-1:nth-child(5) p:first-child span,
.table-view .row-1:nth-child(9) p:first-child span {
  color: #204181;
}

.table-view .row-1:last-child p:first-child {
  font-size: 1.6rem;
  color: #5e6977;
  font-weight: 600;
}

#managers {
  display: none;
  padding: 4rem 2.4rem;
}

#managers .managers {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2.4rem;
}

.managers .manager {
  height: 27.2rem;
  border-radius: 1rem;
  background-color: #204181;
  background-image: url("../../../assets/images/manager-pattern.png");
  background-size: cover;
  box-sizing: border-box;
  padding: 2.4rem;
  color: #ffff;
}

.manager-head {
  display: flex;
  gap: 1.2rem;
  align-items: center;
}

.manager-head img {
  width: 5rem;
  height: 4.6rem;
}

.manager-head h1 {
  font-size: 2.4rem;
  border-bottom: 0.1rem solid rgb(167, 177, 188, 0.2);
  padding-bottom: 1.6rem;
  width: 100%;
}

.manager .manager-info {
  display: flex;
  justify-content: space-between;
  margin-top: 2.4rem;
  margin-bottom: 3.2rem;
}

.manager .manager-info p {
  font-size: 4.8rem;
}

.manager .manager-info p span {
  font-size: 1.4rem;
  opacity: 0.6;
  display: block;
}

.manager .manager-cta {
  padding: 0.85rem 0;

  font-size: 1.4rem;
  font-family: "Lato", sans-serif;
  font-weight: bold;
  color: #fff;
  border: none;
  background-color: #f6821f;
  width: 100%;
  border-radius: 1rem;
  cursor: pointer;
}

.manager-modal {
  transform: scale(0);
  padding: 2.8rem 0rem;
  padding-bottom: 0rem;
  box-sizing: border-box;
  position: fixed;
  width: 100rem;
  height: 51rem;
  background-color: #ffff;
  border-radius: 1.6rem;
  top: 13.5%;
  left: 15%;
  box-shadow: 0rem 2rem 5rem rgba(0, 0, 0, 0.1);

  z-index: 99;
  transform-origin: bottom left;
  transition: transform 0.5s ease;
}

.manager-modal-head {
  display: flex;
  justify-content: space-between;
  padding: 0rem 3.2rem;
}

.modal-close {
  width: 1.32rem;
  height: 1.32rem;
  cursor: pointer;
}

.manager-modal-head .manager-name {
  display: flex;
  gap: 1.1rem;
  align-items: center;
}

.manager-modal-head .manager-name img {
  width: 5rem;
  height: 4.6rem;
}

.manager-name > div h2 {
  color: #4a5462;
  font-size: 2rem;
}

.manager-name > div p {
  color: #a7b1bc;
  font-size: 1.2rem;
  font-weight: normal;
}

.manager-table {
  margin-top: 3.6rem;
  width: 100%;
  height: 82%;
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 0 3.2rem;
  box-sizing: border-box;
}

.manager-table::-webkit-scrollbar {
  width: 0.6rem;
}

.manager-table::-webkit-scrollbar-track {
  background-color: transparent;
}

.manager-table::-webkit-scrollbar-thumb {
  background: #f6821f;
  border-radius: 1.6rem;
  border: 0.3rem solid #f6821f;
}

.manager-table .table-head,
.manager-table .row {
  display: flex;
}

.manager-table .table-head p:first-child,
.row p:first-child {
  width: 40%;
}

.manager-table .table-head p,
.row p {
  width: 15%;
  font-size: 1.4rem;
}

.manager-table .table-head p {
  color: #a7b1bc;
  padding-bottom: 0.7rem;
}

.manager-table .row p {
  padding: 2.4rem 0;
  font-size: 1.6rem;
}

.manager-table .row p:first-child {
  color: #a7b1bc;
  font-weight: normal;
  font-size: 1.4rem;
}

.manager-table .row p:first-child span {
  display: block;
  color: #204181;
  font-weight: bold;
}

.dashed {
  width: 100%;
  height: 0.1rem;
  background-image: url("../../../assets/images/Line 31.svg");
  background-size: contain;
}

.solid {
  border: none;
  border-bottom: 0.1rem solid rgb(167, 177, 188, 0.2);
}

.bottom-bg-gradient {
  width: 100%;
  height: 9%;
  position: sticky;
  bottom: 0rem;
  background-image: linear-gradient(
    0deg,
    #ffffff 0%,
    rgba(255, 255, 255, 0.09) 202.22%
  );
  left: 0;
}

#indicators {
  display: none;
  padding: 0 2.3rem 0 2.3rem;
}

#indicators .risk-ratio-info {
  margin: 4rem 0rem 3.2rem 0;
}

#indicators .equity h1,
#indicators .debt h1 {
  font-size: 2.4rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  color: #000;
  margin-bottom: 1.8rem;
}

.equity-info,
.debt-info {
  margin-bottom: 6.4rem;
  gap: 4rem;
}

.equity-info .col-1,
.debt-info .col-1 {
  border-right: 0.1rem solid rgb(167, 177, 188, 0.2);
}

.equity-info .col-1,
.equity-info .col-2,
.debt-info .col-1,
.debt-info .col-2 {
  width: 50%;
}

.equity-info .col-1 > div,
.equity-info .col-2 > div,
#indicators > div .equity-info,
.debt-info .col-1 > div,
.debt-info .col-2 > div,
#indicators > div .debt-info {
  display: flex;
}

.equity-info .col-1 > div p,
.equity-info .col-2 > div p,
.debt-info .col-1 > div p,
.debt-info .col-2 > div p {
  font-size: 1.4rem;
  padding: 1.2rem 0;
}

.equity-info .col-1 > div p:first-child,
.equity-info .col-2 > div p:first-child,
.debt .col-1 > div p:first-child,
.debt .col-2 > div p:first-child {
  width: 75%;
  color: #848484;
}

.equity-info .col-1 > div p:last-child,
.equity-info .col-2 > div p:last-child,
.debt-info .col-1 > div p:last-child,
.debt-info .col-2 > div p:last-child {
  color: #204181;
}

.debt-info {
  margin-bottom: 5.5rem;
}

#main-other-content {
  padding: 0 2.3rem;
  padding-bottom: 2rem;
}

#main-other-content div h1 {
  font-size: 2.4rem;
}

#main-other-content .top-10 .top-10-heading,
.risk-ratio .risk-ratio-head {
  display: flex;
  justify-content: space-between;
}

.top-10 .top-10-heading a,
.risk-ratio .risk-ratio-head a {
  font-size: 1.8rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  color: #f6821f;
}

.top-10 .top-10-heading a img,
.risk-ratio .risk-ratio-head a img {
  width: 0.66rem;
  height: 1.2rem;
}

.top-10 .top-10-table {
  margin-top: 2.9rem;
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 4rem;
}

.top-10 .top-10-table thead {
  font-size: 1.4rem;
  color: #a7b1bc;
}

.top-10-table thead tr td {
  border-bottom: 0.1rem solid rgb(167, 177, 188, 0.2);
  padding: 0.8rem 0;
}

.top-10-table thead tr td img {
  width: 1.7rem;
  height: 1.1rem;
}

.top-10 .top-10-table tbody {
  font-size: 1.6rem;
  color: #000;
}

.top-10 .top-10-table tbody tr td {
  padding: 0.8rem 0;
}

.top-10-table thead tr td:nth-child(2),
.top-10 .top-10-table tbody tr td:nth-child(2) {
  width: 25%;
}

#main-other-content .sect-comp .sect-comp-info {
  display: flex;
  gap: 9rem;
  align-items: center;
}

.sect-comp .sect-comp-table {
  margin-top: 2.4rem;
  width: 100%;
  margin-left: 2rem;
  margin-bottom: 4rem;
}

.sect-comp-table tr td {
  padding: 0.6rem 0;
}

.sect-comp-table tr td:first-child {
  font-family: "Inter", sans-serif;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  gap: 0.7rem;
  font-weight: normal;
  color: #374151;
}

.sect-comp-table tr td:first-child img {
  width: 1rem;
  height: 1rem;
}

.sect-comp-table tr td:last-child {
  font-size: 1.6rem;
  font-weight: bold;
  color: #000;
}

.sect-comp .pie-chart {
  width: 25.6rem;
  height: 25.6rem;
  margin-right: 6rem;
}

.market-cap .cap {
  display: flex;
  font-size: 2rem;
  color: #fff;
  width: 100%;
  margin: 3.2rem 0 4rem 0;
}

.cap > div {
  padding: 1.5rem;
  box-sizing: border-box;
  border-radius: 2.7rem;
  position: relative;
}

.cap .large {
  background-color: #f97b65;
  width: 32.6%;
  padding-left: 2.4rem;
  z-index: 2;
}
.cap .mid {
  background-color: #fcc04e;
  width: 55%;
  padding-left: 6.9rem;
  margin-left: -4.9rem;
  z-index: 1;
}
.cap .small {
  background-color: #5f6c8f;
  width: 40.5%;
  padding-left: 7.9rem;
  margin-left: -5rem;
  z-index: 0;
}

.risk-ratio-info {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding: 2rem 0rem;
  background-color: #204181;
  border-radius: 1.2rem;
  margin-top: 2.4rem;
  margin-bottom: 4rem;
}

.risk-ratio-info div {
  border-right: 0.1rem solid rgb(167, 177, 188, 0.2);
  padding-left: 2.1rem;
}

.risk-ratio-info div:last-child {
  border: none;
}

.risk-ratio-info div p {
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  color: #a7b1bc;
  gap: 0.56rem;
  margin-bottom: 3.7rem;
}

.risk-ratio-info div p img {
  width: 1.7rem;
  height: 1.7rem;
}

.risk-ratio-info div h1 {
  font-size: 4.8rem !important;
  color: #ffff;
}

.key-features h1 {
  margin-bottom: 2.4rem;
}

.key-features .key-card-container {
  display: flex;
  margin-bottom: 1.6rem;
  gap: 1.6rem;
}

.key-card-container .key-card {
  padding: 1.9rem 2.4rem;
  background-color: #fef9ed;
  color: #4a5462;
  border-radius: 1.2rem;
}

.key-card-container .key-card h2 {
  font-size: 1.6rem;
  display: flex;
  align-items: center;
  gap: 1.2rem;
}

.key-card-container .key-card h2 img {
  width: 1.4rem;
  height: 1.4rem;
}

.key-card-container .key-card:first-child h2 img {
  width: 1.4rem;
  height: 1.8rem;
}

.key-card-container .key-card p {
  font-size: 1.4rem;
  margin-top: 1.3rem;
  font-weight: normal;
}

.key-view-more {
  display: none;
  padding: 0 1.2rem;
  margin-bottom: 2.2rem;
}

.key-view-more .dashed {
  background-image: url("../../../assets/images/Line 31.svg");
  height: 0.1rem;
  width: 100%;
  display: block;
}

.key-view-more div {
  display: flex;
}

.key-view-more div p {
  padding: 2.4rem 0;
}

.key-view-more div p:first-child {
  font-size: 1.6rem;
  color: #204181;
  width: 45%;
}

.key-view-more div p:last-child {
  font-size: 1.4rem;
  color: #4a5462;
  font-weight: normal;
}

.key-features .key-cta {
  display: flex;
  justify-content: center;
}

.key-cta .key-features-view {
  margin: 0.8rem 0 3.3rem 0;
  font-family: "Lato", sans-serif;
  border: 0.15rem solid #f6821f;
  border-radius: 1rem;
  color: #f6821f;
  font-size: 1.7rem;
  background-color: #ffff;
  padding: 1.5rem 4.1rem;
  font-weight: bold;
  cursor: pointer;
}

/* mobile css */

.bottom-etf-mobile {
  display: none !important;
}
.risko-meter-for-mobile {
  display: none !important;
}
.display-none-in-dekstop {
  display: none !important;
}
@media (min-width:320px) and (max-width:480px) {
  #main {
    display: block !important;
    padding: 0rem 0rem 0 0rem !important;
  }
  .etf-details .inner-etf {
    border-bottom-left-radius: 1.6rem;
    border-bottom-right-radius: 1.6rem;
    border-top-right-radius: 0rem;
    border-top-left-radius: 0rem;
    padding: 2.4rem 3.2rem;
  }
  .graph-vs {
    display: block !important;
  }
  .graph-vs > p {
    font-size: 2rem;
    text-align: center;
  }
  .side-content-for-desktop {
    display: none !important;
  }
  .graph .graph-img {
    width: 47.9rem;
    height: 39.3rem;
  }
  .graph-filter-bar {
    gap: 4rem;
    margin-bottom: 0rem;
  }
  /* #details-news > .details-news {
    display: none;
  } */
  #d-footer .d-footer-links {
    gap: 4rem !important;
  }
  .etf-details .bottom-etf {
    gap: 3.2rem 9.5rem;
  }
  .risk-ratio-info div h1 {
    font-size: 1.8rem !important;
    color: #ffff;
  }
  #primarynav {
    display: none;
  }
  .sect-comp .pie-chart {
    width: 15.6rem;
    height: 15.6rem;
    margin-right: 0rem;
  }
  #main-other-content .sect-comp .sect-comp-info {
    display: flex;
    gap: 6rem;
    align-items: center;
  }
  .etf-details .bottom-etf {
    grid-template-columns: repeat(2, 1fr);
  }
  .bottom-etf-desktop {
    display: none !important;
  }
  .bottom-etf-mobile {
    display: block !important;
  }
  .passive-mobile {
    position: absolute;
    top: 4rem;
  }
  .etf-main h2 {
    display: flex;
    align-items: center;
    font-size: 1.6rem;
    gap: 1rem;
    /* margin-bottom: -1.5rem; */
    margin-top: 4rem;
  }
  .graph-vs > div {
    padding: 2rem 2rem;
    gap: 13.3rem !important;
  }
  .graph-vs > p {
    margin: 1rem 0rem;
  }
  .graph-head span {
    margin-top: 2.5rem;
  }
  .top-10-table thead tr td {
    width: 53%;
  }
  .top-10-holding-heading-mobile {
    padding-right: 5rem !important;
