
a {
    text-decoration: none;
    font-size: 1.7rem;
}
  
ul {
    list-style: none;
}
#primarynav {
    padding: 1.1rem 11.3rem;
    width: 83%;
    position: fixed;
    background-color: #fff;
    z-index: 5;
    /* padding-bottom: 5.1rem; */
    display: flex;
    gap: 9.8rem;
    align-items: center;
    /* background-color: #f6821f; */
  }
  
  #primarynav a  img {
    width: 9.7rem;
    height: 5.7rem;
  }
  
  #primarynav .nav-options,
  #primarynav .nav-cta {
    display: flex;
    font-size: 1.7rem;
  }
  
  .nav-options li {
    padding: 3rem 0;
  }
  
#primarynav .nav-options li a,
  .nav-cta li:first-child a {
    color: #1d263a;
    padding: 2rem 0;
  }
  
  #primarynav .nav-options {
    gap: 5.6rem;
    flex: 1;
  }
  
  #primarynav .nav-cta {
    gap: 4rem;
  }
  
  .nav-cta li:last-child a {
    color: #ffff;
    background-color: #f6821f;
    border-radius: 1rem;
    padding: 2rem 4.2rem;
  }
  