@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter&display=swap");


#main {
    padding: 0 11.3rem;
    display: flex;
    gap: 2.4rem;
    position: relative;
    top: 10rem;
  }

  #main-content,
  #side-content {
    display: flex;
    flex-direction: column;
    gap: 3.2rem;
  }
  #side-content_mobile {
    display: flex;
    flex-direction: column;
    gap: 3.2rem;
  }
  #main-content {
    flex: 0 0 66%;
    padding-bottom: 6.4rem;
  }

  #side-content {
    flex: 0 0 31.8%;
  }
#side-content-mobile {
    flex: 0 0 31.8%;
  }
  #main-content > .etf-details,
  #main-content > .main-container,
  #side-content > .returns,
  #side-content > .riskometer,
  #side-content > .cal-returns,
  #side-content > .documents {
    border-radius: 1.6rem;
    background-color: #ffff;
    box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    width: 100%;
  }
#side-content-mobile > .returns,
  #side-content-mobile > .riskometer,
  #side-content-mobile > .cal-returns,
  #side-content-mobile > .documents {
    border-radius: 1.6rem;
    background-color: #ffff;
    box-shadow: 0 2rem 5rem rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    width: 100%;
  }
  #side-content > .returns,
  #side-content > .riskometer,
  #side-content > .cal-returns,
  #side-content > .documents {
    padding: 2.4rem;
  }
#side-content-mobile > .returns,
  #side-content-mobile > .riskometer,
  #side-content-mobile > .cal-returns,
  #side-content-mobile > .documents {
    padding: 2.4rem;
  }
  #details-news {
    padding: 8rem 6rem 9.7rem 6rem;
    background-color: #f2f6fa;
    display: flex;
    margin-top: 15rem;
  }

  #details-news > .details-news {
    background-color: #ffff;
    box-shadow: 0 5.5rem 11.4rem rgba(0, 0, 0, 0.1);
    border-radius: 1rem;
    padding: 4.8rem 5.7rem;
    box-sizing: border-box;
  }

  .details-news .d-news-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .details-news .d-news-heading h1 {
    font-size: 4.2rem;
    color: #4a5462;
  }

  .details-news .d-news-heading a {
    color: #000;
    border: 0.15rem solid #000;
    border-radius: 1rem;
    padding: 1.6rem 2.9rem;
  }

  .details-news .d-news-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.4rem;
    margin-top: 6.4rem;
  }

  .d-news-container .d-news-card img {
    width: 26vw;
  }

  .d-news-card .d-news-date {
    font-size: 1.3rem;
    color: #989898;
    margin: 4.8rem 0 2.3rem 0;
  }
  .d-news-card .d-news-desc {
    font-size: 2.4rem;
    color: #4a5462;
    margin-bottom: 4.7rem;
  }

  #details-footer {
    background: #f2f6fa;
    padding: 5rem 6rem;
    padding-top: 0;
  }

  #d-footer {
    background-color: #e6eaf4;
    display: flex;
    justify-content: space-between;
    padding: 6.3rem;
    border-radius: 2.5rem;
  }

  #d-footer .d-footer-links {
    display: flex;
    list-style: none;
    gap: 6rem;
  }

  #d-footer .d-footer-logo p {
    color: #1d263a;
    font-size: 1.5rem;
    opacity: 0.7;
  }

  #d-footer .d-footer-logo img {
    width: 9.6rem;
    mix-blend-mode: multiply;
    margin-bottom: 3.1rem;
  }

  #d-footer .d-footer-links li a {
    text-decoration: none;
    color: #1d263a;
    opacity: 0.7;
  }

  @media (min-width: 1000px) and (max-width: 1050px) {
    .news-container .news-card img {
      width: 26.3vw;
    }
  }

  @media (min-width: 590px) and (max-width: 768px) {
    .news-container .news-card img {
      width: 26.3vw;
    }
  }

  @media (min-width: 412px) and (max-width: 589px) {
    .news-container .news-card img {
      width: 26.3vw;
    }
  }

  @media (min-width: 320px) and (max-width: 411px) {
    .news-container .news-card img {
      width: 26.3vw;
    }
  }
