@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter&display=swap");
#main {
    display: flex;
    padding: 4.1rem 11.3rem 0 11.3rem;
    gap: 2rem;
  }
  
  #main .side-content {
    flex: 0 0 26.4%;
    box-sizing: border-box;
  }
  
  #main .main-content {
    flex: 0 0 71.6%;
    box-sizing: border-box;
  }
  
  .main-content > h1 {
    font-size: 4.2rem;
    color: #213053;
  }
  
  .main-content .inputs {
    display: flex;
    gap: 3.3rem;
    align-items: center;
    margin: 4.8rem 0;
  }
  
  .inputs .input-bar {
    flex: 1;
    background: rgba(0, 0, 0, 0.04);
    border-radius: 4.3rem;
    padding: 1.16rem 2.6rem;
    display: flex;
    gap: 2.5rem;
    align-items: center;
  }
  
  .inputs .input-bar img {
    width: 1.8rem;
    height: 1.8rem;
    opacity: 0.6;
  }
  
  .inputs .input-bar input {
    background-color: transparent;
    border: none;
    color: #4a5462;
    opacity: 0.6;
    font-size: 1.4rem;
    font-family: "Lato", sans-serif;
    flex: 1;
  }
  
  .inputs .basket-etf-toggler {
    display: flex;
    position: relative;
    background-color: #f1f5fc;
    color: #a7b1bc;
    font-size: 1.5rem;
    border-radius: 1.8rem;
    padding: 0.8rem 2rem;
    width: 22.3rem;
    justify-content: space-between;
    cursor: pointer;
    box-sizing: content-box;
  }
  
  .inputs .basket-etf-toggler .switch {
    position: absolute;
    width: 50%;
    height: 100%;
    color: #ffff;
    background: #f47a23;
    top: 0;
    left: 0;
    border-radius: 1.8rem;
    padding: 0.8rem 0;
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    transition: left 0.4s ease;
  }
  
  .main-content .etf-baskets {
    display: none;
  }
  
  .main-content .individual-etfs > h2,
  .main-content .etf-baskets > h2 {
    font-size: 2.4rem;
    color: #4a5462;
    margin-bottom: 1.6rem;
  }
  
  .main-content .individual-etfs .recent-launch {
    margin-top: 4.8rem;
  }
  