.side-content {
    background: rgb(242, 246, 250, 0.34);
    border-radius: 1.2rem;
    box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25);
    font-size: 1.4rem;
    padding: 2.4rem 0;
    height: fit-content;
  }
  
  .side-content .filters-hr {
    border: none;
    border-top: 0.1rem solid #a7b1bc;
    opacity: 0.2;
  }
  
  .side-content .filters-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #4a5462;
    padding: 0 2.4rem;
    padding-bottom: 2rem;
  }
  
  .filters-head h2,
  .percentage-ret h2,
  .min-investment h2,
  .filter-risk h2,
  .filter-market-cap h2,
  .platform h2,
  .fitlter-cri-rating h2,
  .filter-class h2 {
    font-size: 1.4rem;
    color: #4a5462;
  }
  
  .filters-head p {
    font-size: 1.2rem;
    cursor: pointer;
  }
  
  .side-content .percentage-ret,
  .side-content .min-investment,
  .side-content .filter-risk,
  .side-content .filter-market-cap,
  .side-content .filter-cri-rating {
    padding: 2rem 2.4rem;
  }
  
  .side-content .platform,
  .side-content .filter-class {
    padding: 2rem 2.4rem 0 2.4rem;
  }
  
  .return-input-bars {
    position: relative;
    width: 100%;
    height: 4.8rem;
  }
  
  .return-input-values {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .return-input-values span {
    font-size: 1.2rem;
    color: #5e6977;
    opacity: 0.42;
  }
  
  .return-input-values input[type="text"] {
    width: 9.4rem;
    height: 3rem;
    text-align: right;
    font-size: 1.2rem;
    font-weight: 700;
    font-family: "Lato", sans-serif;
    color: #4a5462;
    border: 0.1rem solid #f2f6fa;
    background: #fff;
    box-sizing: border-box;
    padding-right: 0.8rem;
  }
  
  .percentage-ret .return-input-bars input[type="range"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100%;
    outline: none;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    background-color: transparent;
    pointer-events: none;
  }
  .percentage-ret .return-input-bars .indivi-return-input-slider-track,
  .percentage-ret .return-input-bars .basket-return-input-slider-track {
    width: 100%;
    height: 0.4rem;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    border-radius: 0.5rem;
  }
  .percentage-ret
    .return-input-bars
    input[type="range"]::-webkit-slider-runnable-track {
    -webkit-appearance: none;
    height: 0.4rem;
  }
  .percentage-ret .return-input-bars input[type="range"]::-moz-range-track {
    -moz-appearance: none;
    height: 0.4rem;
  }
  .percentage-ret .return-input-bars input[type="range"]::-ms-track {
    appearance: none;
    height: 0.4rem;
  }
  .percentage-ret .return-input-bars input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 1.6rem;
    width: 1.6rem;
    background-color: #ff7a00;
    cursor: pointer;
    margin-top: -0.7rem;
    pointer-events: auto;
    border-radius: 50%;
  }
  .percentage-ret .return-input-bars input[type="range"]::-moz-range-thumb {
    -webkit-appearance: none;
    height: 1.6rem;
    width: 1.6rem;
    cursor: pointer;
    border-radius: 50%;
    background-color: #ff7a00;
    pointer-events: auto;
  }
  .percentage-ret .return-input-bars input[type="range"]::-ms-thumb {
    appearance: none;
    height: 1.6rem;
    width: 1.6rem;
    cursor: pointer;
    border-radius: 50%;
    background-color: #ff7a00;
    pointer-events: auto;
  }
  
  .min-investment .invest-amount-input {
    margin-top: 2.4rem;
  }
  
  .invest-amount-bubble {
    position: relative;
    width: 5.8rem;
    height: 1.8rem;
    background-color: #f6821f;
    color: #fff;
    font-size: 1.2rem;
    padding: 0.7rem;
    text-align: center;
    margin-bottom: 0.6rem;
    border-radius: 0.5rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
  }
  
  .invest-amount-bubble:after {
    content: " ";
    position: absolute;
    width: 0;
    height: 0;
    left: auto;
    right: 45%;
    bottom: -1.3rem;
    border: 0.7rem solid;
    border-color: #f6821f transparent transparent;
  }
  
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider {
    -webkit-appearance: none;
    width: 100%;
    cursor: pointer;
  }
  
  /*progress support*/
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider.slider-progress {
    --range: calc(var(--max) - var(--min));
    --ratio: calc((var(--value) - var(--min)) / var(--range));
    --sx: calc(0.05 * 1.6rem + var(--ratio) * (100% - 1.6rem));
  }
  
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider:focus {
    outline: none;
  }
  
  /*webkit*/
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 1.6rem;
    background: #f6821f;
    border: none;
    margin-top: calc(0.4rem * 0.5 - 1.6rem * 0.5);
  }
  
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider::-webkit-slider-runnable-track {
    height: 0.4rem;
    border: none;
    border-radius: 0.5rem;
    background: rgb(167, 177, 188, 0.3);
    box-shadow: none;
  }
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider.slider-progress::-webkit-slider-runnable-track {
    background: linear-gradient(to right, darkorange, darkorange) 0 / var(--sx)
        100% no-repeat,
      rgb(167, 177, 188, 0.3);
  }
  
  /*mozilla*/
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider::-moz-range-thumb {
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 1.6rem;
    background: #f6821f;
    border: none;
  }
  
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider::-moz-range-track {
    height: 0.4rem;
    border: none;
    border-radius: 0.5rem;
    background: rgb(167, 177, 188, 0.3);
    box-shadow: none;
  }
  
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider.slider-progress::-moz-range-track {
    background: linear-gradient(to right, darkorange, darkorange) 0 / var(--sx)
        100% no-repeat,
      rgb(167, 177, 188, 0.3);
  }
  
  /*ms*/
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider::-ms-fill-upper {
    background: transparent;
    border-color: transparent;
  }
  
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider::-ms-fill-lower {
    background: transparent;
    border-color: transparent;
  }
  
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider::-ms-thumb {
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 1.6rem;
    background: #f6821f;
    border: none;
    margin-top: 0;
    box-sizing: border-box;
  }
  
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider::-ms-track {
    height: 0.4rem;
    border-radius: 0.5rem;
    background: rgb(167, 177, 188, 0.3);
    border: none;
    box-shadow: none;
    box-sizing: border-box;
  }
  
  .min-investment
    .invest-range
    .invest-amount-input
    input[type="range"].styled-slider.slider-progress::-ms-fill-lower {
    height: 0.4rem;
    border-radius: 0.5rem 0 0 0.5rem;
    margin: -undefined 0 -undefined -undefined;
    background: linear-gradient(to right, darkorange, darkorange);
    border: none;
    border-right-width: 0;
  }
  
  .checkboxes {
    display: flex;
    flex-direction: column;
    margin-top: 1.8rem;
    gap: 1rem;
  }
  
  .checkboxes .check-row {
    display: flex;
    align-items: center;
    gap: 1.6rem;
    color: #5e6977;
  }
  
  .checkboxes .check-row input[type="checkbox"],
  .checkboxes .check-row input[type="radio"] {
    appearance: none;
    width: 1.6rem;
    height: 1.6rem;
    border: 0.1rem solid #4a5462;
    border-radius: 0.4rem;
    cursor: pointer;
  }
  
  .checkboxes .check-row input[type="checkbox"]:checked,
  .checkboxes .check-row input[type="radio"]:checked {
    border: none;
    background-image: url('../../../assets/images/orange-checkbox.svg');
    background-position: center;
    background-size: cover;
  }
  
  .checkboxes .check-row label img {
    width: 1.15rem;
    height: 1.1rem;
    margin-right: 0.5rem;
  }
  
  .checkboxes .check-row input[type="radio"] {
    border-radius: 50%;
  }
  
  .checkboxes .check-row input[type="radio"]:checked {
    background-image: url('../../../assets/images/radio-checked.svg');
  }
  
  .etf-baskets-filters {
    display: none;
  }
  